const test = require('ava')
const {decode} = require('ini')

const {getFileFromContainer} = require('./util')

const getFactomdConf = async config => decode(await getFileFromContainer('factomd.conf', {
  network: 'main',
  ...config
}))

const optionNameTest = (yamlName, optionName, config) => test(`${yamlName} -> ${optionName}`, async t => {
  const factomdConf = await getFactomdConf(config)
  t.is(config[yamlName], factomdConf.app[optionName])
})

optionNameTest('apiPassword', 'FactomdRpcPass', {apiPassword: 'password', apiUser: 'username'})